
.app {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: black;
}

.myName {
    color: white;
    font-weight: bold;
    font-size: 68px;
}

.myIntro {
    color: white;
    font-size: 23px;
    font-weight: bold;
    margin-top: 10px;
    width: 400px;
}

.lightName{
    color: rgba(255, 255, 255, 0.5);
}

.leftView {
    display: flex;
    flex-direction: column;
    align-items: center;
    transition-duration: 1.5s;
    transition-property: left;
    position: relative;
    left: -20%;
    margin: 20px;
}

.leftView.animateStart {
    left: 0;
}

.myPhoto {
    height: 250px;
    width: 250px;
    border-radius: 50%;
}

.bigImage {
    height: 450px;
    width: 450px;
    border-radius: 50%;
}

.myPhotoWrapper {
    justify-content: center;
    align-items: center;
    align-self: center;
    margin-top: 20px;
}

.rightView {
    position: relative;
    right: -30%;
    margin: 100px;
    transition-duration: 1.5s;
    transition-property: right;
}

.rightView.animateStartAgain {
    right: 0;
}


@keyframes left-to-right {
    0% {
        left: 0;
        width: 0%;
    }

    50% {
        width: 35%;
    }

    100% {
        left: 100%;
        width: 0%;
    }
}

@keyframes right-to-left {
    0% {
        right: 0;
        width: 0%;
    }

    50% {
        width: 35%;
    }

    100% {
        right: 100%;
        width: 0%;
    }
}
